$font-family_1: var(--font-family);

.Ecommers__head_lib {
	background: -moz-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: -webkit-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: -o-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: -ms-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
}
.Ecommers__head_lib-text {
	font-family: $font-family_1;
	font-weight: 800;
	font-size: 25px;
	line-height: 75px;
	letter-spacing: -0.04em;
	margin: 10px 10px 10px;
}
.Ecommers__head_lib-content {
	display: grid;
	grid-template-columns: auto auto;
}
.Ecommers__head_lib-index {
	text-align: center;
	background: var(--color-footer);
	background: -moz-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: -webkit-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: -o-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: -ms-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
	filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	border-radius: 20px;
	margin-top: 20px;
	margin-bottom: 20px;
	margin-left: 200px;
	margin-right: 200px;
}

@media screen and (max-width: 850px) {
	.Ecommers__head_lib-text {
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 0rem;
	}
	.Ecommers__head_lib-content {
		display: grid;
	}
	.Ecommers__head_lib-index {
		margin-top: 10px;
		margin-bottom: 10px;
		margin-left: 50px;
		margin-right: 50px;
	}
}
