$font-family_1: var(--font-family);

.Ecommers__head_about {
	background: -moz-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: -webkit-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: -o-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: -ms-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
	background: radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
}
.Ecommers__head_about-index {
	display: flex;
	flex-direction: column;
	padding: 3rem;
	background: var(--color-footer);
	background: -moz-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: -webkit-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: -o-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: -ms-radial-gradient(
		circle at 30% -100%,
		#042c54 25%,
		rgba(4, 44, 84, 1) 85%,
		rgba(27, 120, 222, 1) 100%
	);
	background: radial-gradient(circle at 30% -100%, #042c54 25%, rgba(4, 44, 84, 1) 85%, rgba(27, 120, 222, 1) 100%);
	filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
	border-radius: 20px;
	margin-top: 3%;
	margin-left: 15%;
	margin-right: 15%;
}
.Ecommers__head_about-title {
	font-family: $font-family_1;
	font-weight: 600;
	font-size: 25px;
}
.Ecommers__head_about-title2 {
	font-family: $font-family_1;
	font-weight: 600;
	font-size: 25px;
}

@media screen and (max-width: 850px) {
	.Ecommers__head_about-title {
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 0rem;
		font-size: 15px;
	}
	.Ecommers__head_about-text {
		margin-top: 1rem;
	}
	.Ecommers__head_about-title2 {
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 0rem;
		font-size: 15px;
		p {
			margin-top: 1rem;
		}
	}
}
